import styled from "styled-components";

const Content3 = styled.div`
  @media only screen and (max-width: 375px) {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-self: flex-end;
    width: 100%;
    background-color: hsl(0, 0%, 95%);
    height: auto;
    
  }
  display: flex;
  flex-direction: row;
  padding: 0%;
  margin: 0%;
  align-items: center;
`;

export default Content3;
